

















































.card {
  width: 100%;
  margin-bottom: 10px;
  &-meta {
    font-size: .9em;
    margin-bottom: 5px;
    span {
      margin-left: 4px;
      margin-right: 4px;
    }
  }
  &-text {
    svg.svg-inline--fa {
      width: 1em;
      vertical-align: -0.125em;
    }
    span {
      margin-left: 2px;
      margin-right: 13px;
    }
  }
}
